<template>
	<view class="wait-lingqu">		
		<view class="item-user">
			<view class="item-name">{{orde.Name}}</view>
			<view class="user-phone">{{orde.Phone}}</view>
		</view>		
		<view class="lingqu-content">		
			<view class="content-info">
				<view class="info-icon">
					<image :src="orde.icon"></image>
				</view>			
				<view class="info-num">				
					<view class="red">{{isRedBall}}</view>
					<view class="blue">{{listArray.BlueBall}}</view>
				</view>
			</view>
		</view>		
		<view class="order-number">
			<view class="order-number-info">
				<view class="order-number-left">订单标号</view>
				<view class="order-number-right">{{orde.OrderNumber}}</view>
			</view>
			<view class="order-number-info">
				<view class="order-number-left">创建时间</view>
				<view class="order-number-right">{{orde.AddTime}}</view>
			</view>
		</view>							
		<view class="lingqu-btn">已中奖</view>
	</view>
</template>
<script>
	import common from '../../common/common'
	var _self;
	export default {
		data() {
			return {
				orde: {},
				listArray:[]			
			}
		},
		computed:{
			isRedBall(){
				if(this.listArray.RedBall){					
					return this.listArray.RedBall.replace(/,/g,"  ")
				}
			}
		},
		onLoad(option) {
			_self = this;			
			if(option.waitList){
				let detail = JSON.parse(decodeURIComponent(option.waitList))
				if (detail.orde.LotteryType === "1") {
					detail.orde.icon = "https://fucai.yiruit.com/UpFiles/shouye/qilecai.png";
					detail.orde.iconName = '七乐彩'
				} else if (detail.orde.LotteryType === "2") {
					detail.orde.icon = "https://fucai.yiruit.com/UpFiles/shouye/shuangseqiu.png";
					detail.orde.iconName = '双色球'
				} else if (detail.orde.LotteryType === "3") {
					detail.orde.icon = "https://fucai.yiruit.com/UpFiles/shouye/fucai3d.png";
					detail.orde.iconName = '福彩3D'
				}else if (detail.orde.LotteryType === "4") {
					detail.orde.icon = "https://fucai.yiruit.com/UpFiles/shouye/scraping.png";
					detail.orde.iconName = '刮刮乐'
				}
				_self.orde = detail.orde;		
				_self.listArray = detail.list[0]				
			}
		}	
	}
</script>
<style lang="scss" scoped>
	.wait-lingqu {
		padding: 35rpx;
		.item-user{
			display: flex;
			flex-direction: column;
			justify-content: space-between;		
			background-color: #fff;
			border-radius: 10rpx;
			padding: 30rpx;
			.user-name{
				color: #333;
				font-size: 16px;
			}
			.user-phone {
				color: #999;
				font-size: 14px;
				margin-top: 10rpx;
			}
		}
		.lingqu-content {
			margin-top: 20rpx;
			padding: 10rpx 20rpx 20rpx;
			border-radius: 10rpx;
			background-color: #fff;
			box-shadow: 2rpx 2rpx 2rpx #eee;
			.content-title {
			    font-size: 16px;
				font-weight: bold;
				color: #333;	
				margin-bottom: 10rpx;
			}
			.content-info {
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				padding: 25rpx 20rpx;
				.info-icon {
					width: 90rpx;
					height: 90rpx;	
					margin-right: 20rpx;
					image {
						display: block;
						width: 100%;
						height: 100%;
					}
				}
				.info-num {
					flex: 1;
					display: flex;
					justify-content: flex-start;
					align-items: center;
					.red{						
						color: red;		
						white-space:pre;
					}
					.blue{
						color: blue;
						margin-left: 16rpx;
					}
				}
			}
		}
		.order-number {
			height: auto;
			margin: 0 auto;
			margin-top: 20rpx;
			background-color: #fff;
			color: #999;			
			padding: 30rpx 20rpx;
			border-radius: 5rpx;
			.order-number-info{
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				font-size: 14px;
				color: #666;	
				margin-bottom: 30rpx;
				&:last-of-type{
					margin-bottom: 0;
				}
			}
		}
		.paizhao{
			height: auto;
			margin: 0 auto;
			margin-top: 20rpx;
			background-color: #fff;
			color: #999;			
			padding:30rpx;
			border-radius: 5rpx;	
			display: flex;
			flex-direction: column;
			align-items: center;
			.paizhao_num{
				font-size: 50px;
				color: #999;				
			}
			text{
				font-size: 14px;
				color: #999;
			}
		}
		.paizhao_info{
			height: auto;
			margin: 0 auto;
			display: flex;
			justify-content: center;
			align-items: center;
			image{
				width: 70%;
			}
		}
		.lingqu-btn {
			position: fixed;
			bottom: 20rpx;
			width: 90%;
			left: 50%;
			transform: translateX(-50%);
			height: 60rpx;
			text-align: center;
			background-color: #fe8685;
			color: #fff;
			line-height: 60rpx;
			border-radius: 30rpx;
		}
	}
</style>
